import React, {useState, useEffect, useRef} from 'react'
import styled from 'styled-components'
import {CgCloseO} from 'react-icons/cg'
import "./PopupDeclaration.css"
import axios from 'axios'

import {selectUser} from './../../features/userSlice'
import {useSelector} from "react-redux";

function PopupDeclaration( { popupGameData, popupMainVideoIndex, declare_visible, setDeclare_visible, declare_part, declare_contents, setDeclare_contents, declare_reviewId } ) {
    const user = useSelector(selectUser);
    const [reportType, setReportType] = useState("")
    const [videoId, setVideoId] = useState(1);
    const declare_ref = useRef();

    function CloseVideoDelaration(){ //  X 
        setDeclare_visible(false);
    }
    function CancleDeclare() { //   
      setDeclare_visible(false);
    }
    function SubmitVideoDeclare() {
      console.log(user.user_id, declare_part, declare_contents, reportType, videoId, declare_ref.current.value);

      if(declare_ref.current.value===""){
          alert("  .")
      }else{  
        if(window.confirm(" ?")===true){
          axios({
              method: 'post',
              url: `http://ec2-3-35-250-221.ap-northeast-2.compute.amazonaws.com:8080/api/user/${user.user_id}/video/${videoId}/report`,
              data: {
                reportType: declare_contents,
                content: declare_ref.current.value
              }
          }).then((res) => {
              if(res){
                  console.log(res);
                  alert("  .");
                  //  response(=review id)   id .
              }
              else
                  alert("Declare's data sending fail");
          })
          setDeclare_visible(false);
          declare_ref.current.value = "";
        }
      }
      

  }
  function SubmitReviewDeclare() {
      console.log(user.user_id, declare_part, declare_contents, declare_reviewId);

      axios({
          method: 'post',
          url: `http://ec2-3-35-250-221.ap-northeast-2.compute.amazonaws.com:8080/api/user/${user.user_id}/review/${declare_reviewId}/report`,
          data: {
            reportType: reportType,
            content: declare_contents
          }
      }).then((res) => {
          if(res){
              console.log(res);
              alert("  .");
              //  response(=review id)   id .
          }
          else
              alert("Declare's data sending fail");
      })
      setDeclare_visible(false);
  }
    function GetReport(e){ // reportType   
        setDeclare_contents(e.target.innerText);
        setReportType(e.target.htmlFor)
    }

    useEffect(() => { // popupGameData  popupMainVideoIndex    '   Id(=videoId)' .
      axios.get(`http://ec2-3-35-250-221.ap-northeast-2.compute.amazonaws.com:8080/api/game/${popupGameData.id}/video/all`)
      .then((res)=>{
          let temp;
          res.data.map((set) => {
              if(set.id === popupMainVideoIndex) {
                  temp = set;
              }
          })
          console.log(temp);
          setVideoId(temp.id);
          // setVideoId(res.data[popupMainVideoIndex].id);
      })
    }, [popupGameData, popupMainVideoIndex]);

    return (
        <DeclareBackground className="declare_background" declare_visible={declare_visible} >
          
            <DeclarationWrapper 
              declare_visible={declare_visible} 
              declare_part={declare_part}
              className="declare_wrapper"
            >
        
                <DeclarationContent className="declare_contents" >
                <CloseDeclarationButton aria-label='close declare' className="declare_closeBtn" onClick={CloseVideoDelaration}/>
                  <h3 className="declare_part">
                    {/* declare_part:1 =  / declare_part:0 =  */}
                      {declare_part ? " " : " "}  
                  </h3><br/>

                  {declare_part ? (
                    <>
                      <div className="declare_item">
                      <input type="radio" name="declare_radio" className="declare_radio" id=""  />
                      <label htmlFor="" className="declare_selection" onClick={GetReport}> </label><br/>
                      </div>
                      <div className="declare_item">
                          <input type="radio" name="declare_radio" className="declare_radio" id="" />
                          <label htmlFor="" className="declare_selection" onClick={GetReport}>   </label><br/>
                      </div>
                      <div className="declare_item">
                          <input type="radio" name="declare_radio" className="declare_radio" id="" />
                          <label htmlFor="" className="declare_selection" onClick={GetReport}>   </label><br/>
                      </div>
                      <div className="declare_item">
                          <input type="radio" name="declare_radio" className="declare_radio" id="" />
                          <label htmlFor="" className="declare_selection" onClick={GetReport}>  </label><br/>
                      </div>
                      <div className="declare_item">
                          <input type="radio" name="declare_radio" className="declare_radio" id="" />
                          <label htmlFor="" className="declare_selection" onClick={GetReport}>    </label><br/>
                      </div> 

                      <hr className="hr_tag"/>
                      <input 
                        className="declare_contents_input"
                        placeholder="  ."
                        ref={declare_ref}
                      />

                      <div className="declare_submit_part">
                          <button onClick={CancleDeclare} className="declare_cancel"></button>
                          {declare_part ? (
                            <button onClick={SubmitVideoDeclare} className="declare_submit"></button>
                          ) : (
                            <button onClick={SubmitReviewDeclare} className="declare_submit"></button>
                          )}
                      </div>      
                    </>             
                  ) : (
                    <>
                      <div className="declare_item">
                      <input type="radio" name="declare_radio" className="declare_radio" id=""  />
                      <label htmlFor="" className="declare_selection" onClick={GetReport}> </label><br/>
                      </div>
                      <div className="declare_item">
                          <input type="radio" name="declare_radio" className="declare_radio" id="" />
                          <label htmlFor="" className="declare_selection" onClick={GetReport}>   </label><br/>
                      </div>
                      <div className="declare_item">
                          <input type="radio" name="declare_radio" className="declare_radio" id="" />
                          <label htmlFor="" className="declare_selection" onClick={GetReport}>   </label><br/>
                      </div>
                      <div className="declare_item">
                          <input type="radio" name="declare_radio" className="declare_radio" id="" />
                          <label htmlFor="" className="declare_selection" onClick={GetReport}>    </label><br/>
                      </div>

                      <hr className="hr_tag"/>

                      <div className="declare_submit_part">
                          <button onClick={CancleDeclare} className="declare_cancel"></button>
                          {declare_part ? (
                            <button onClick={SubmitVideoDeclare} className="declare_submit"></button>
                          ) : (
                            <button onClick={SubmitReviewDeclare} className="declare_submit"></button>
                          )}
                      </div>     
                    </>
                  )}


                </DeclarationContent>
            
            </DeclarationWrapper>
        
        </DeclareBackground>
    )
}
const DeclareBackground = styled.div`
  box-sizing: border-box;
  display: ${(props) => (props.declare_visible ? 'block' : 'none')};
  position: fixed;
  overflow: auto;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.6);
  z-index: 1000;
`

const DeclarationWrapper = styled.div`
  box-sizing: border-box;
  display: ${(props) => (props.declare_visible ? 'block' : 'none')};
  position: fixed;
  top: 20%;
  left: 20%;
  right: 20%;
  bottom: 20%;
  height: ${(props) => (props.declare_part ? '520px' : '450px')};
  width: 700px;
  max-width: 1500px;
  z-index: 1000;
  overflow: hidden;
  outline: 0;
  border-radius: 10px;
`


const DeclarationContent = styled.div`
  box-sizing: border-box;
  position: relative;
  overflow: hidden;
  box-shadow: 0 0 6px 0 rgba(0, 0, 0, 0.5);
  background-color: #111;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  // height: 1000px;
  // width: 1500px;

  height: 100%;
  width: 100%;

  max-width: 1500px;
  top: 50%;
  transform: translateY(-50%);
  margin: 0 auto;
  padding: 50px 50px;
`

const CloseDeclarationButton = styled(CgCloseO)`
    cursor: pointer;
    color: #fff;
    position: absolute;
    top: 20px;
    right: 10px;
    width: 30px;
    height: 30px;
    padding: 0;
    z-index: 10;
`

export default PopupDeclaration
