import React, {useCallback, useEffect, useState} from "react";
import axios from "axios";
import {useDispatch, useSelector} from "react-redux";
import {selectUser, login} from "../../features/userSlice";
import Footer from "../../Footer";
import Nav from "../../Nav";
import './MembershipScreen.css';
import {useHistory} from "react-router-dom";
import bronze from '../../img/bronze-medal.png';
import silver from '../../img/silver-medal.png';
import gold from '../../img/gold-medal.png';
import SearchScreen from "../search/SearchScreen";

const tiers = [
    {
        title: 'Basic',
        price: '4500',
        description: ['   3 ',],
        buttonText: 'Sign up for free',
        buttonVariant: 'outlined',
    },
    {
        title: 'Premium',
        price: '9900',
        description: ['   3 ',],
        buttonText: 'Contact us',
        buttonVariant: 'outlined',
    },
];

function MembershipScreen() {
    const user = useSelector(selectUser);
    const [loading, setLoading] = useState(true);
    const history = useHistory();
    const dispatch = useDispatch();

    const [searching, setSearching] = useState(false);
    const [searchWord, setSearchWord] = useState("");
    const [membershipData, setMembershipData] = useState([]);
    const [membership, setMembership] = useState([]);


    useEffect(() => {
        async function fetchMembershipData() {
            await axios.get(`http://ec2-3-35-250-221.ap-northeast-2.compute.amazonaws.com:8080/api/membership/all`)
                .then( (res) => {
                    setMembershipData(res.data);
                }).catch((err)=> {
                     console.log(err);
                });
            return membershipData;
        }
        fetchMembershipData();
    }, [])

    const { IMP } = window;
    IMP.init('imp40158151');

    const payment = () => {
        const info = RegisterMembership();

        const customerUid = user.name + '_' + user.user_id;

        IMP.request_pay({
            merchant_uid : 'merchant_' + new Date().getTime(),
            name : '',
            amount : `${info.price}`, //      .
            customer_uid : `${customerUid}`, //customer_uid      .
            buyer_email : 'iamport@siot.do',
            buyer_name : `${user.name}`,
            buyer_tel : '02-1234-1234'
        }, rsp => {
            if (rsp.success) {
                //   
                // axios HTTP 
                console.log(rsp);
                axios({
                    url: `http://ec2-3-35-250-221.ap-northeast-2.compute.amazonaws.com:8080/api/user/${user.user_id}/membership/${info.membershipId}`, //  
                    method: "put",
                    headers: { "Content-Type": "application/json" }
                }).then(() =>{
                    axios.get(`http://ec2-3-35-250-221.ap-northeast-2.compute.amazonaws.com:8080/api/user/${user.user_id}`)
                    .then((res) => {
                        console.log(res.data);
    
                        dispatch(login({
                            membershipName : res.data.membershipName,
                            availableCoupon : res.data.availableCoupon,
                            price: res.data.price
                        }))
                        window.localStorage.setItem("membershipName", res.data.membershipName);
                        window.localStorage.setItem("availableCoupon", res.data.availableCoupon);
                        window.localStorage.setItem("price", res.data.price);
                    }).then(() =>{
                        alert(" .");
                        history.push("/");
                        window.location.reload();
                    })
                })

            } else {
                //   
                alert(" .");
            }
        });
    };

    useEffect(() => {


        setLoading(false);
        return () => {
            setLoading(true);
        }
    }, [searching]);

    const getTierImg = (index) => {
        if(index == 0)
            return (<img src={bronze} alt="bronze"/>)
        if(index == 1)
            return (<img src={gold} alt="gold"/>)
    }

    function RegisterMembership() {
        const info = new Object();
        const checked = document.getElementsByName("membership_selectBtn");

        checked.forEach((set) => {
            if (set.checked === true) {
                info.price = set.value[0];
                info.price += set.value[1];
                info.price += set.value[2];
                info.price += set.value[3];
                info.membershipId = set.value[5];

            }
        })
        return info;
    }

    if(loading) return (<div>Loading...</div>);
    return (
        <>
            <div className="membershipScreen">
                <Nav setSearchWord={setSearchWord} setSearching={setSearching} />
                { searching ? (
                    <SearchScreen searchWord={searchWord} />
                ) : (
                    <>
                        <div className="membershipScreen_body">
                            <h2></h2>
                            <h3>        !</h3>
                            <div className="membershipScreen_result">
                                <div className="membership_container">
                                    {membershipData.map((tier, index) => (
                                        <label className="membership_item" htmlFor={tier.name}>
                                            <input
                                                key={index}
                                                type="radio"
                                                className="membership_check"
                                                name="membership_selectBtn"
                                                id={tier.name}
                                                value={[tier.price, tier.id]}
                                                defaultChecked={index == 0}
                                            />
                                            <div className="item_inner">
                                                <div className="membership_img">
                                                    {getTierImg(index)}
                                                </div>
                                                <h3 className="membership_name">{tier.name}<br/></h3>
                                                <h4 className="membership_description">   {tier.couponNum}<br/></h4>
                                                <h6 className="membership_price"> {tier.price}</h6>
                                            </div>
                                        </label>
                                    ))}
                                </div>
                            </div>
                            <div className="membership_submit">
                                <button className="membership_btn" onClick={payment}></button>
                            </div>
                        </div>
                    </>
                )}
            </div>
            <Footer />
        </>
    )
}

export default MembershipScreen;
